import { Component, OnInit } from '@angular/core';
import {EventsModel} from '../events.model';
import {ActivatedRoute, Router} from '@angular/router';
import {Store} from '@ngrx/store';
import {EventState} from '../store/events.reducer';
import * as EventAction from '../../events/store/events.actions';

@Component({
  selector: 'app-manageevents',
  templateUrl: './manageevents.component.html',
  styleUrls: ['./manageevents.component.css']
})
export class ManageeventsComponent implements OnInit {

  events: EventsModel[];
  currentEvent: EventsModel;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private store: Store<EventState>
  ) {
  }

  ngOnInit() {
    this.store.dispatch(new EventAction.TryFetchEvents());
    this.store.select('events').subscribe(eventState => {
      return this.events = eventState.events;
    });
  }

  editEvent(index) {
    this.router.navigate(['../editEvent', index], {relativeTo: this.route});
  }

  deleteEvent(index: number) {
    this.currentEvent = this.events[index];
    this.currentEvent.delFlg = true;
    this.store.dispatch(new EventAction.TrySaveEvent(this.currentEvent));
    this.store.dispatch(new EventAction.TryFetchEvents());
  }
}
