import {Component, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from 'rxjs';
import {Store} from '@ngrx/store';
import {DemandState} from '../../store/demand.reducer';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../../shared/serverEndpoints';
import * as DemandActions from '../../store/demand.actions';

@Component({
  selector: 'app-eventlist',
  templateUrl: './eventlist.component.html',
  styleUrls: ['./eventlist.component.css']
})
export class EventlistComponent implements OnInit, OnDestroy {

  allEvents: Array<any>;
  eventSubscription: Subscription;
  requestData: {
    location: 'null',
    maxPeople: null,
    date: null
  };
  isSearched: boolean;

  constructor(private store: Store<DemandState>, private route: ActivatedRoute, private router: Router, private http: HttpClient) {
  }

  ngOnInit() {
    this.http.post(endPoints.eventQuery, this.requestData, {observe: 'response'}).subscribe(
      (response: HttpResponse<any>) => {
        if (response.status === 200) {
          this.store.dispatch(new DemandActions.SetAllEvent(response.body));
        }
      }
    );
    this.isSearched = false;

    this.eventSubscription = this.store.select('demand').subscribe(
      demandState => {
        if (demandState.eventDemand.allEvents) {
          this.allEvents = [];
          demandState.eventDemand.allEvents.forEach(
            event => {
              this.allEvents.push(event);
            }
          );
        }
      }
    );
  }

  searchList() {
    this.isSearched = true;
    console.log(this.allEvents);
  }

  navigateToEventDetail(id: string) {
    this.router.navigate(['../details', id], {relativeTo: this.route});
  }

  ngOnDestroy(): void {
    this.allEvents = null;
    this.eventSubscription.unsubscribe();
  }


}
