import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {EventsModel} from '../../../roles/business/events/events.model';
import {EventPersonDetailsModel} from '../eventPersonDetails.model';
import {ActivatedRoute} from '@angular/router';
import {HttpClient, HttpResponse} from '@angular/common/http';
import {Store} from '@ngrx/store';
import {AppState} from '../../../store/app.reducer';
import {DemandState} from '../../store/demand.reducer';
import {ToastService} from '../../../shared/toast/toast.service';
import * as endPoints from '../../../shared/serverEndpoints';
import {CashfreeRequestInterface} from '../../interface/cashfree.model';
import * as cashfree from '../../../../assets/js/cashfree';

@Component({
  selector: 'app-eventcheckout',
  templateUrl: './eventcheckout.component.html',
  styleUrls: ['./eventcheckout.component.css']
})
export class EventcheckoutComponent implements OnInit {

  @ViewChild('pgForm', {static: true}) paymentGatewayFormElement: ElementRef;
  paymentGatewayForm: FormGroup;

  userForm: FormGroup;
  name = '';
  email = '';
  mobile = '';
  id = '';
  activeEvent: EventsModel;
  selectedPersonDetails: EventPersonDetailsModel;
  finalPrice: number;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private http: HttpClient,
    private store: Store<AppState>,
    private demandStore: Store<DemandState>,
    private toast: ToastService
  ) {
  }

  ngOnInit() {

    this.userForm = this.fb.group({
      name: this.fb.control(''),
      email: this.fb.control(''),
      mobile: this.fb.control('')
    });

    this.demandStore.select('demand').subscribe(
      demandState => {
        if (demandState.eventDemand.activeEvent) {
          this.activeEvent = demandState.eventDemand.activeEvent;
        }
        if (demandState.eventDemand.selectedDetails) {
          this.selectedPersonDetails = demandState.eventDemand.selectedDetails;
        }
        if (demandState.finalPrice) {
          this.finalPrice = demandState.finalPrice;
        }
      }
    );

    this.store.select('authState').subscribe(
      value => {
        if (value.authenticated) {
          this.name = value.profile.name;
          this.email = value.profile.email;
          this.mobile = value.profile.mobile;
          this.userForm.patchValue(value.profile);
        }
      }
    );
    this.paymentGatewayForm = this.fb.group({
      appId: [''],
      orderId: [''],
      orderCurrency: [''],
      orderNote: [''],
      customerName: [this.name],
      customerEmail: [this.email],
      returnUrl: [''],
      signature: [''],
      notifyUrl: [''],
      orderAmount: [''],
      customerPhone: [this.mobile],
    });
  }

  onSubmit() {
    const uuid = 'xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
      let dt = new Date().getTime();
      const r = (dt + Math.random() * 16) % 16 | 0;
      dt = Math.floor(dt / 16);
      return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
    const data = {
      days: {...this.selectedPersonDetails.days},
      mobile: this.userForm.controls[`mobile`].value,
      email: this.userForm.controls[`email`].value,
      name: this.userForm.controls[`name`].value,
      bookingType: 'event',
      bookingStatus: 'pending',
      noOfBookings: {...this.selectedPersonDetails},
      amount: this.finalPrice,
      orderId: uuid
    };

    this.http.post(endPoints.newBooking, data, {observe: 'response'})
      .subscribe((response: HttpResponse<CashfreeRequestInterface>) => {
        if (response.status === 200) {
          cashfree.f(response.body);
        }
      });
  }

  getUserDetails(val: number) {
    if (val > 5999999999) {
      this.http.post(endPoints.getUserByMobile, {mobile: val}, {observe: 'response'}).subscribe(
        (response: HttpResponse<any>) => {
          if (response.status === 200) {
            this.toast.showSuccess('Congratulations! User found.');
            this.userForm.patchValue(response.body);
          } else {
            this.toast.showDanger('No user found!! Enter your details.');
          }
        }
      );
    } else {
      this.toast.showDanger('Invalid Mobile Number !!');
    }
  }

}
