import {Component, ElementRef, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {EventsModel} from '../events.model';
import {NgbCalendar, NgbDate, NgbDateParserFormatter} from '@ng-bootstrap/ng-bootstrap';
import {ActivatedRoute, ParamMap, Router} from '@angular/router';
import {Store} from '@ngrx/store';
import {EventState} from '../store/events.reducer';
import {ToastService} from '../../../../shared/toast/toast.service';
import * as EventActions from '../store/events.actions';

interface EventDays {
  activityDescription;
  trekDayImage;
  locations;
  provided: {
    water: [false],
    food: [false],
    sleepingBag: [false]
  };
  carry;
  transport;
  title;
  meetup;
  notes;
}

@Component({
  selector: 'app-addevents',
  templateUrl: './addevents.component.html',
  styleUrls: ['./addevents.component.css']
})
export class AddeventsComponent implements OnInit, OnDestroy {

  @ViewChild('daylocation', {static: false}) daylocation: ElementRef;
  @ViewChild('meetup', {static: false}) meetup: ElementRef;
  @ViewChild('location', {static: false}) location: ElementRef;
  @ViewChild('t', {static: false}) tabSet;

  basicForm: FormGroup;
  eventForm: FormGroup;
  settingForm: FormGroup;
  eventDayDetails: FormArray;

  finalEvent = new EventsModel();

  hoveredDate: NgbDate;
  fromDate: NgbDate;
  toDate: NgbDate;
  days: any;
  currentDay: number;
  eventDaysArray: Array<any>;
  eventDaysArrayDummy: Array<any>;

  eventDayImg: File = null;
  eventImg: File = null;
  eventImgArray: Array<any> = [];
  imgPreviewUrlEventDay: any = '../../../assets/icons/uploadPic.png';
  imgPreviewUrlEvent: any = '../../../assets/icons/uploadPic.png';

  id: number;
  editMode = false;

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private calendar: NgbCalendar,
    public formatter: NgbDateParserFormatter,
    private store: Store<EventState>,
    private route: ActivatedRoute,
    private toastService: ToastService
  ) {
    this.currentDay = 0;
    this.days = 3;
    this.eventDaysArrayDummy = Array(this.days).fill(0);
    this.eventDaysArray = [];
    this.fromDate = calendar.getToday();
    this.toDate = calendar.getNext(calendar.getToday(), 'd', this.days - 1);
  }

  ngOnInit() {

    this.basicForm = this.fb.group({
      meetup: [''],
      expType: [''],
      location: ['', Validators.required],
      eventDates: this.fb.array([], Validators.required),
      totalDays: ['', Validators.required],
      languages: this.fb.group({
        hindi: [false],
        english: [false]
      }),
      ownerDescription: [''],
    });

    this.eventForm = this.fb.group({
      activityDescription: [''],
      eventDayImage: [''],
      locations: ['', Validators.required],
      provided: this.fb.group({
        water: [false],
        food: [false],
        sleepingBag: [false]
      }),
      carry: [''],
      transport: [''],
      title: ['', Validators.required],
      meetup: [''],
      notes: [''],
    });

    this.settingForm = this.fb.group({
      eventImage: this.fb.array([]),
      provided: this.fb.group({
        accommodation: [false],
        food: [false],
        transport: [false]
      }),
      carry: [''],
      pricePerPerson: ['', Validators.required],
      minPeople: [''],
      maxPeople: [''],
      todoPlan: [''],
      category: [''],
      eventTitle: ['', Validators.required],
    });

    this.eventDayDetails = this.fb.array([]);

    this.store.select('events').subscribe(eventState => {
      if (!this.editMode) {
        if (eventState.activeEvent) {
          this.finalEvent = eventState.activeEvent;
          if (eventState.activeEvent.eventDayDetails) {
            this.eventDaysArray = eventState.activeEvent.eventDayDetails;
          }
        }
      }
    });

    // for editing a event
    this.route.paramMap.subscribe((params: ParamMap) => {
        if (params.get('id')) {
          this.editMode = true;

          this.id = +params.get('id');
          this.store.select('events').subscribe(eventState => {
            this.finalEvent = eventState.events[this.id];

            if (this.finalEvent) {
              this.basicForm.patchValue(this.finalEvent);
              this.settingForm.patchValue(this.finalEvent);

              this.finalEvent.eventDayDetails.forEach((value: EventDays) => {
                this.eventDayDetails.push(this.addEventDayDetailGroup(value));
              });

              this.eventForm.setValue(this.eventDayDetails.value[0]);
            } else {
              this.router.navigate(['../../manageEvent'], {relativeTo: this.route});
            }
          });
        }
      }
    );

  }

  addEventDayDetailGroup(value: EventDays) {
    const group = this.fb.group({
      activityDescription: [value.activityDescription],
      eventDayImage: [''],
      locations: [value.locations],
      provided: this.fb.group({
        water: [value.provided.water],
        food: [value.provided.food],
        sleepingBag: [value.provided.sleepingBag]
      }),
      carry: [value.carry],
      transport: [value.transport],
      title: [value.title],
      meetup: [value.meetup],
      notes: [value.notes],
    });

    return group;

  }

  onDateSelection(date: NgbDate) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }

    if (this.toDate && this.fromDate) {
      this.days = Math.floor((Date.parse(this.toDate.month + '/' + this.toDate.day + '/' + this.toDate.year) - Date.parse(this.fromDate.month + '/' + this.fromDate.day + '/' + this.fromDate.year)) / 86400000);
      this.days += 1;
    }
    this.eventDaysArrayDummy = Array(this.days).fill(0);
  }

  isHovered(date: NgbDate) {
    return this.fromDate && !this.toDate && this.hoveredDate && date.after(this.fromDate) && date.before(this.hoveredDate);
  }

  isInside(date: NgbDate) {
    return date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return date.equals(this.fromDate) || date.equals(this.toDate) || this.isInside(date) || this.isHovered(date);
  }

  validateInput(currentValue: NgbDate, input: string): NgbDate {
    const parsed = this.formatter.parse(input);
    return parsed && this.calendar.isValid(NgbDate.from(parsed)) ? NgbDate.from(parsed) : currentValue;
  }

  addEventDate() {
    const x = {
      startDate: this.fromDate,
      toDate: this.toDate
    };
    const obj = new FormControl(x);
    (this.basicForm.controls[`eventDates`] as FormArray).push(obj);
    this.basicForm.patchValue({totalDays: this.days});
  }

  removeEventDate(i: number) {
    (this.basicForm.controls[`eventDates`] as FormArray).removeAt(i);
  }

  imgPreviewEventDay(fileInput: any) {
    this.eventDayImg = fileInput.target.files[0] as File;
    const mimeType = this.eventDayImg.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }
    const reader = new FileReader();
    reader.readAsDataURL(this.eventDayImg);
    reader.onload = () => {
      this.imgPreviewUrlEventDay = reader.result;
    };
  }

  imgPreviewEvent(fileInput: any) {
    const files = fileInput.target.files;
    if (files.length >= 5) {
      for (const image of files) {
        this.eventImgArray.push(image);
        const reader = new FileReader();
        reader.onload = (e: any) => {
          (this.settingForm.get('eventImage') as FormArray).push(
            this.fb.group({
              url: e.target.result
            })
          );
        };
        reader.readAsDataURL(image);
      }
    } else {
      this.toastService.showDanger('Please select more than 4 images!');
    }
  }

  setEventDay(it: number) {
    this.currentDay = it;
    if (this.eventDayDetails.value[it]) {
      this.eventForm.setValue(this.eventDayDetails.value[it]);
    } else {
      this.eventForm.reset();
    }
  }

  basicFormSubmit() {
    this.basicForm.controls[`meetup`].patchValue(this.meetup.nativeElement.value);
    this.basicForm.controls[`location`].patchValue(this.location.nativeElement.value);
    this.dispatch(this.basicForm.value);
    this.tabSet.select('eventForm');
  }

  eventFormSubmit() {
    this.eventForm.controls[`locations`].patchValue(this.daylocation.nativeElement.value);
    if (this.eventDayDetails && this.eventDayDetails.value[this.currentDay]) {
      this.eventDayDetails.insert(this.currentDay, this.addEventDayDetailGroup(this.eventForm.value));
    } else {
      this.eventDayDetails.push(this.addEventDayDetailGroup(this.eventForm.value));
    }
    console.log(this.eventDayDetails.value);
    this.dispatch({eventDayDetails: this.eventDayDetails.value});

    const data = new FormData();
    data.append('_id', this.finalEvent._id);
    data.append('eventDayImage', this.eventDayImg);
    data.append('index', (this.currentDay).toString());
    this.store.dispatch(new EventActions.TryUploadEventImage(
      data
    ));

    if (this.currentDay === this.days - 1) {
      this.tabSet.select('settingForm');
      this.currentDay = 0;
    } else {
      this.currentDay += 1;
    }
  }

  settingFormSubmit() {
    this.dispatch(this.settingForm.value);
    if (this.eventImgArray.length) {
      const data = new FormData();
      data.append('_id', this.finalEvent._id);
      Array.from(this.eventImgArray).forEach(image => {
        data.append('eventImage[]', image);
      });
      this.store.dispatch(new EventActions.TryUploadEventImage(
        data
      ));
    }
    this.router.navigate(['../manageEvent'], {relativeTo: this.route});
  }

  private dispatch(obj) {
    Object.assign(this.finalEvent, obj);
    this.store.dispatch(new EventActions.TrySaveEvent(this.finalEvent));
  }

  ngOnDestroy(): void {
    this.finalEvent = null;
    this.store.dispatch(new EventActions.SetActiveEvent(null));
  }
}
