import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {EventsModel} from '../../../roles/business/events/events.model';
import {Store} from '@ngrx/store';
import {DemandState} from '../../store/demand.reducer';
import {ActivatedRoute, ParamMap, Router} from '@angular/router';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as DemandActions from '../../store/demand.actions';
import * as endPoints from '../../../shared/serverEndpoints';

@Component({
  selector: 'app-eventdetails',
  templateUrl: './eventdetails.component.html',
  styleUrls: ['./eventdetails.component.css']
})
export class EventdetailsComponent implements OnInit {
  queryForm: FormGroup;
  children = 0;
  adult = 1;
  id: string;
  activeEvent: EventsModel;
  similarEvent: EventsModel[] = [];
  eventBannerImages = new Array(5);
  finalPrice = 0;

  selectedDatesIndex = 0;

  constructor(
    private store: Store<DemandState>,
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private http: HttpClient
  ) {
  }

  ngOnInit() {

    this.route.paramMap.subscribe((params: ParamMap) => {
        if (params.get('id')) {
          this.id = params.get('id');

          this.store.dispatch(new DemandActions.TryFetchEvent({_id: this.id}));

          // initializing form to get pricing info
          this.queryForm = this.fb.group({
            days: this.fb.control(''),
            adults: this.fb.control(this.adult),
            children: this.fb.control(this.children),
            _id: this.fb.control(this.id),
          });

          this.getPricing();
        }
      }
    );
    this.store.select('demand').subscribe(
      demandState => {
        if (demandState.eventDemand.activeEvent) {
          this.activeEvent = demandState.eventDemand.activeEvent;
          this.eventBannerImages = demandState.eventDemand.activeEvent.eventImage;
          if (demandState.eventDemand.activeEvent.similarEvents.length > 0) {
            demandState.eventDemand.activeEvent.similarEvents.forEach(event => {
              this.similarEvent.push(event);
            });
          }
          console.log(this.similarEvent);
        }

      }
    );
  }

  adultControl(x) {
    if (!(this.adult < 2 && x < 0)) {
      this.adult += x;
    }
    this.queryForm.controls[`adults`].patchValue(this.adult);
    this.getPricing();
  }

  childControl(x) {
    if (!(this.children < 1 && x < 0)) {
      this.children += x;
    }
    this.queryForm.controls[`children`].patchValue(this.children);
    this.getPricing();
  }

  getPricing() { // http request to fetch final pricing
    this.http.post(endPoints.eventPricing, this.queryForm.value, {observe: 'response'}).subscribe(
      (response: HttpResponse<any>) => {
        if (response.status === 200) {
          this.finalPrice = response.body;
        }
      }
    );
  }

  selectedDatesIndexFunction(val: number) {
    this.selectedDatesIndex = val;
  }

  onSubmitQueryForm() {
    this.queryForm.controls[`days`].patchValue(this.activeEvent.eventDates[this.selectedDatesIndex]);
    this.store.dispatch(new DemandActions.SetActiveEventDetails(this.queryForm.value));
    this.store.dispatch(new DemandActions.SetPrice(this.finalPrice));
    this.router.navigate(['../../checkout'], {relativeTo: this.route});
  }


}
