import * as endPoints from '../../../../shared/serverEndpoints';
import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as EventActions from './events.actions';
import {map, switchMap} from 'rxjs/operators';
import {EventsModel} from '../events.model';

@Injectable()
export class EventsEffects {
  constructor(public action: Actions, public http: HttpClient) {
  }

  @Effect()
  uploadImage = this.action.pipe(
    ofType(EventActions.TRY_UPLOAD_EVENT_IMAGE),
    map((data: EventActions.TryUploadEventImage) => {
      return data.payload;
    }),
    switchMap((data: any) => {
      return this.http.post(endPoints.uploadEventImage, data, {observe: 'response'});
    }), switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: EventActions.SET_ACTIVE_EVENT,
            payload: response.body
          }
        ];
      } else {
        alert('Some error occured !');
        return [];
      }
    })
  );

  @Effect()
  editEvent = this.action.pipe(
    ofType(EventActions.TRY_SAVE_EVENT),
    map((data: EventActions.TrySaveEvent) => {
      return data.payload;
    }),
    switchMap((data: EventsModel) => {
      return this.http.post(endPoints.saveEvent, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: EventActions.SET_ACTIVE_EVENT,
            payload: response.body
          }
        ];
      } else {
        alert('Some error occured !');
        return [];
      }
    })
  );


  @Effect()
  fetchEvents = this.action.pipe(
    ofType(EventActions.TRY_FETCH_EVENTS),
    switchMap(() => {
      return this.http.get(endPoints.fetchEvents, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        console.log('inside fetch trek 200');
        return [
          {
            type: EventActions.SET_EVENTS,
            payload: response.body
          }
        ];
      } else {
        // alert('Some error occured !');
        return [];
      }
    })
  );


}
