import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {EventsComponent} from './events.component';
import {AddeventsComponent} from './addevents/addevents.component';
import {ManageeventsComponent} from './manageevents/manageevents.component';
import {DashboardComponent} from './dashboard/dashboard.component';


const routes: Routes = [
  {
    path: '', component: EventsComponent, children: [
      {path: '', redirectTo: 'dashboard'},
      {path: 'dashboard', component: DashboardComponent},
      {path: 'addEvent', component: AddeventsComponent},
      {path: 'editEvent/:id', component: AddeventsComponent},
      {path: 'manageEvent', component: ManageeventsComponent},
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EventsRoutingModule {
}
