import {Action} from '@ngrx/store';
import {EventsModel} from '../events.model';

export const TRY_SAVE_EVENT = 'TRY_SAVE_EVENT';
export const TRY_FETCH_EVENTS = 'TRY_FETCH_EVENTS';
export const TRY_UPLOAD_EVENT_IMAGE = 'TRY_UPLOAD_EVENT_IMAGE';
export const SET_EVENTS = 'SET_EVENTS';
export const SET_ACTIVE_EVENT = 'SET_ACTIVE_EVENT';

export class TryUploadEventImage implements Action {
  readonly type = TRY_UPLOAD_EVENT_IMAGE;

  constructor(public payload: any) {
  }
}

export class TryFetchEvents implements Action {
  readonly type = TRY_FETCH_EVENTS;
}

export class TrySaveEvent implements Action {
  readonly type = TRY_SAVE_EVENT;

  constructor(public payload: EventsModel) {
  }
}


export class SetEvents implements Action {
  readonly type = SET_EVENTS;

  constructor(public payload: EventsModel[]) {
  }
}

export class SetActiveEvent implements Action {
  readonly type = SET_ACTIVE_EVENT;

  constructor(public payload: EventsModel) {
  }
}

export type EventsActions =
  SetActiveEvent |
  TryUploadEventImage |
  SetEvents |
  TryFetchEvents |
  TrySaveEvent ;

